"use server"; // üëà ÏÑúÎ≤Ñ Ï†ÑÏö© ÌååÏùºÏûÑÏùÑ Î™ÖÏãú

import Parser from "rss-parser";
import translate from "@iamtraction/google-translate";
import { FeedItem } from "@/types"; // üí° 1. types/index.d.ts ÏóêÏÑú ÌÉÄÏûÖ Í∞ÄÏ†∏Ïò§Í∏∞

const RSS_FEED_URL = "https://blog.google/products/gemini/rss/";

export async function getGeminiProductFeed(): Promise<FeedItem[]> {
  try {
    const response = await fetch(RSS_FEED_URL, {
      next: { revalidate: 3600 },
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch RSS feed: ${response.statusText}`);
    }

    const xmlText = await response.text();

    const parser = new Parser<Record<string, never>, FeedItem>({
      customFields: {
        item: [["content:encoded", "content"]], // üëà Ï†ÑÏ≤¥ Î≥∏Î¨∏ Îß§Ìïë
      },
    });

    const feed = await parser.parseString(xmlText);

    const plainItems: FeedItem[] = feed.items.map((item) => ({
      title: item.title,
      link: item.link,
      pubDate: item.pubDate,
      guid: item.guid,
      content: item.content, // üëà Ï†ÑÏ≤¥ Î≥∏Î¨∏
      contentSnippet: item.contentSnippet, // üëà ÏöîÏïΩ
      categories: item.categories,
    }));

    // --- üí° [ÏöîÏ≤≠ÌïòÏã† Î≥∏Î¨∏ Î≤àÏó≠ Î°úÏßÅ] ---
    const translatedItems = await Promise.all(
      plainItems.map(async (item) => {
        try {
          const [translatedTitle, translatedSnippet, translatedContent] =
            await Promise.all([
              translate(item.title, { from: "en", to: "ko" }),
              translate(item.contentSnippet || "", { from: "en", to: "ko" }),
              // üí° [Ï∂îÍ∞Ä] Î≥∏Î¨∏(HTML) Î≤àÏó≠
              translate(item.content, { from: "en", to: "ko" }),
            ]);

          return {
            ...item,
            title: translatedTitle.text,
            contentSnippet: translatedSnippet.text,
            content: translatedContent.text, // üí° Î≤àÏó≠Îêú Î≥∏Î¨∏ÏúºÎ°ú ÎçÆÏñ¥Ïì∞Í∏∞
          };
        } catch (error) {
          console.warn("Translation failed for item:", item.title);
          console.log(error);
          return item; // Î≤àÏó≠ Ïã§Ìå® Ïãú ÏõêÎ≥∏(ÏòÅÏñ¥) Î∞òÌôò
        }
      })
    );

    return translatedItems;
  } catch (error) {
    console.error("Error fetching or parsing Gemini RSS feed:", error);
    return [];
  }
}